import React, { useEffect } from 'react';

const FlourishEmbed = (props) => {
	const iframeStyle = {
			height: '600px',
			width: '100%'
		}

	// similar to ComponentDidMount & ComponentDidUpdate 
	useEffect(() => {
		setTimeout(()=> {
			const iframe = document.querySelector('.flourish-embed iframe');
			iframe.src = iframe.src.replace(/#slide-.*/, '') + '#slide-' + props.index;		
		}, 5000)
		
	});
	
	console.log(props.embedID)
	
	return (
		<div className='flourish-embed' data-src={props.embedID}></div>
	);
}

export default FlourishEmbed;
